<template>
  <div class="home">
    <div class="input">
      <input type="text" v-model="value" placeholder="请输入您要查询的快递单号" @focus="isShowHistory=true" @blur ="hideHistory">
      <div class="close" @click="value=''"></div>
      <div class="search" @click="searchFn"></div>
    </div>
    <div class="history" v-if="isShowHistory&&expressNum.length>0">
      <div class="historyItem" @click="selectExpressNum(code)" v-for="(code,index) in expressNum" :key="index">{{code}}</div>
    </div>
    <div class="searchResult">
      <h3>搜索结果</h3>
      <div class="content" v-if="list.length>0">
        <div class="header">
          <div class="time">
            <div class="text">时间</div>
            <div class="icon" @click="sort=!sort">
              <span class="topTri" :class="{active:sort}"></span>
              <span class="bottomTri" :class="{active:!sort}"></span>
            </div>
          </div>
          <div class="address">地点和跟踪进度</div>
        </div>
        <div class="list">
          <div class="listItem" v-for="(item,i) in showlist " :key = "i">
            <div class="listTime">
              <div class="data">{{item.time.split(' ')[0]}}</div>
              <div class="hour">{{item.time.split(' ')[1]}}</div>
            </div>
            <div class="midStyle">
              <div class="circle" 
              :class="{active:(sort&&i==0)||((!sort)&& i == (showlist.length-1))}"></div>
              <div class="line"></div>
            </div>
            <div class="listAddress">
              {{item.status}}
            </div>
          </div>
        </div>
      </div>
      <div v-else>
        <h5 style="color:#ccc">尚无搜索结果</h5>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import axios from 'axios'

export default {
  name: 'Home',
  data() {
    return {
      value:'',
      list:[],
      sort:true,
      isShowHistory:false,
      expressNum:[]
    }
  },
  mounted() {
    this.expressNum = localStorage.expressNum?JSON.parse(localStorage.expressNum):[];
  },

  components: {

  },
  computed: {
    showlist:function(){
      if(this.sort){
        return [...this.list]
      }else{
        return[...this.list].reverse()
      }
    }
  },
  methods: {
   async searchFn(){
     let httpUrl =`https://qyexpress.market.alicloudapi.com/composite/queryexpress?number=${this.value}`
     let config = {
       headers:{
         'Authorization':'APPCODE 123d27b6bd544efd863651bc52525ff7'
       }
     }
      let res = await axios.get(httpUrl,config)
      this.list = res.data.data.list
      this.saveExpressHistory()
      console.log(res)
    },
    saveExpressHistory(){
      this.expressNum = localStorage.expressNum?JSON.parse(localStorage.expressNum):[]
      if(this.expressNum.indexOf(this.value)==-1){
      this.expressNum.push(this.value)
      localStorage.expressNum = JSON.stringify(this.expressNum)
      }
    },
    selectExpressNum(code){
      this.isShowHistory =false;
      this.value = code;
      this.searchFn();
    },
    hideHistory(){
      setTimeout(()=>this.isShowHistory=false,50)
    }
  },
}
</script>

